"use client"

import { useEffect, useState } from "react"
import { Button } from "@/components/ui/button"
import Link from "next/link"
import { createClient } from "@supabase/supabase-js"

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL!,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
)

export function MainSection() {
  const [content, setContent] = useState<any>({
    title: "",
    highlight: "",
    description: "",
    primary_button: { text: "", link: "" },
    secondary_button: { text: "", link: "" }
  })

  useEffect(() => {
    async function fetchData() {
      const { data, error } = await supabase
        .from("site_content")
        .select("content")
        .eq("type", "main_section")
        .single()

      if (!error && data?.content) {
        setContent(data.content)
      }
    }
    fetchData()
  }, [])

  // title ichidagi highlight span bilan ajratib beriladi
  const parts = content.title.split(content.highlight)

  return (
    <div className="mx-auto max-w-4xl text-center">
      <h1 className="text-balance text-5xl font-bold tracking-tight sm:text-6xl lg:text-7xl">
        {parts[0]}
        <span className="text-primary">{content.highlight}</span>
        {parts[1]}
      </h1>

      <p className="mt-6 text-pretty text-lg text-muted-foreground sm:text-xl">
        {content.description}
      </p>

      <div className="mt-10 flex justify-center gap-4">
        <Button asChild size="lg">
          <Link href={content.primary_button.link}>
            {content.primary_button.text}
          </Link>
        </Button>

        <Button asChild variant="outline" size="lg">
          <Link href={content.secondary_button.link}>
            {content.secondary_button.text}
          </Link>
        </Button>
      </div>
    </div>
  )
}
